
.title {
  text-align: center;
  margin-left: -45px;
}
.enter {
  text-align: center;
}
.enter .newItem {
  border-radius: 5px;
  border: 1px solid #ccc;
  height: 25px;
}
.wrap {
  text-align: center;
}
.wrap .items {
  list-style: decimal;
  display: inline-block;
  margin-left: 90px;
  width: 490px;
}
.wrap .items .item {
  height: 40px;
  margin-bottom: 5px;
  text-align: left;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.wrap .items .item .button.fade-enter-active,
.wrap .items .item .button.fade-leave-active {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.wrap .items .item .button.fade-enter,
.wrap .items .item .button.fade-leave-active {
  opacity: 0;
}
.wrap .items .item .text {
  width: 70%;
  height: 70%;
  border-radius: 5px;
  border: 1px solid #ccc;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.wrap .items .item.fade-enter-active {
  -webkit-animation: bounce-in 0.5s;
          animation: bounce-in 0.5s;
}
.wrap .items .item.fade-leave-active {
  -webkit-animation: bounce-out 0.5s;
          animation: bounce-out 0.5s;
}
@-webkit-keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@-webkit-keyframes bounce-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
}
@keyframes bounce-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
}


